<template>
    <div class="bgbox">
        <router-link to="/player">
          <div class="left_bottom">
              <div class="musicimg"><img class="swipe-img" :src="smimgs"></div>
                 <ul>
                    <li>{{songname}}</li>
                    <li class="smli">{{songername}}</li>
                 </ul>     
          </div>
        </router-link>
       <div class="button">
           <audio :src="music" id="audio"></audio>
			<div class="audio-box bbb">
					<span class="time-font">
						{{second/60 > 9 ? parseInt(second/60) : '0'+parseInt(second/60) }}:{{second%60 >= 10 ? parseInt(second%60) : '0'+parseInt(second%60)}}
					</span>
				    <el-progress :percentage="progressing_audio" status="success" :stroke-width="4"  style="margin-top:.1rem;margin-left:.3rem;float:left;width:3.14rem;"></el-progress>
				    <img :src="audioPlayShow ? imgA : imgB" class="rong-circle-control" @click="controlAudio()">
			</div>
            <!-- <img class="swipe-img1" :src="require('../assets/style/images/home-player_06.png')">
            <img class="swipe-img2" :src="require('../assets/style/images/home-player_09.png')"> -->
        </div>

    </div>
</template>

<script>
import tryaudio from '@/assets/style/mp3/red.mp3';
import song1 from '@/assets/style/mp3/red.mp3';
import song2 from '@/assets/style/mp3/frx.mp3';
import song3 from '@/assets/style/mp3/qlx.mp3';
import song4 from '@/assets/style/mp3/yq.mp3';
import song5 from '@/assets/style/mp3/Planet.mp3';
import audioStart from '@/assets/style/images/home-player_06.png';
import audioStop from '@/assets/style/images/home-player_07.png';
import smimg1 from '@/assets/style/images/home-player_01.png';
import smimg2 from '@/assets/style/images/home-player_02.png';
import smimg3 from '@/assets/style/images/home-player_03.png';
import smimg4 from '@/assets/style/images/home-player_04.png';
import smimg5 from '@/assets/style/images/home-player_05.png';
	export default {
		name: '',
		props: {
			params: {
				type: Object,
				default: () => ({
					message: 'hello'
				})
			}
		},
		data() {
	      	return {
		        'progressing_audio' :0,
		        // 'music' : tryaudio,
		        'playFlag' : false ,
		        'audioPlayShow' : true,
		        "timer" : "" ,
		        "length-music" : 0 , 
		        'second' : 0,
		        "imgA" : audioStart,
		        "imgB" : audioStop
	     	};
		},
		computed: {
        songname () {
        return this.$store.state.songname
       },
       songername () {
        return this.$store.state.songername
       },
       smimgs () {
        return this.$store.state.smimgs
	   },
	   music () {
         return this.$store.state.music
        },
    }, 
	    methods:{
	    	controlAudio : function (){
	    		if ( !this.params){
	    			return false 
	    		} else {
		    		//console.log(this.params);
		    		this.music = this.params.url;
		    		let audio0 = document.getElementById('audio');
		    		let leng = audio0.duration;
		    		//console.log(audio0.duratuion)
		    		if ( this.audioPlayShow ){
		    		 	 audio0.play() ; 
		    		 	
		    		 	 this.audioPlayShow = false ;
		    		 	 this.timer = setInterval( () => {
		    		 	 	//console.log(1/leng);
		    		 	 	this.progressing_audio = (this.progressing_audio/100 + 0.1/leng)*100 ;
		    		 	 	this.second = this.second + 0.1 ; 
		    		 	 	if (this.progressing_audio >= 100 ) {
		    		 	 		clearInterval(this.timer);
		    		 	 		this.progressing_audio = 0; 
		    		 	 		this.second = 0 ;
		    		 	 	}
		    		 	 	//console.log(this.progressing_audio);
				　　　　　}, 100)     		 	 
		    		} else {
		    		 	 audio0.pause()
		    		 	 this.audioPlayShow = true ;
		    		 	 clearInterval(this.timer);
		    		}
	    		}
	    	},
	    }
	}
</script>

<style lang="stylus" scoped>
.left_bottom .musicimg img{
    width 65px
    position absolute
    margin-top -8%
    margin-left 5%
}
.left_bottom ul{
    margin-left 22%
}
.left_bottom ul li{
    color black
    line-height 16px
}
.left_bottom ul .smli{
    color #606060
    font-size 10px
}
.button{
    float right
    margin-right 5%
    margin-top -5%
}
.button img{
    width 27.6px
    float left
    margin-left -10px
}
.swipe-img1{
    margin-top -10px
}
.swipe-img2{
    margin-top -8px
}
.bgbox{
    background-color:rgba(255,255,255,0.85)
   color:#ffffff;
   z-index: 9999;
   position: fixed;
   width: 100%
   height: 70.5px;
   bottom 0px
}
.rong-circle-control {
		width : .56rem ; 
		height : .56rem ;
		border-radius : 50% ; 
	}
	.time-font{
		margin-top:.1rem;
		font-size:.24rem;
		font-family:PingFangSC-Regular;
		font-weight:400;
		color:rgba(153,153,153,1);
		line-height:.34rem;
		float:left ;
		margin-left : 0rem ;
		
	}
	.audio-box {
		height : .56rem ; 
	}

</style>